import React from "react";
import classes from "./Bio.module.css";
import avatar from "../../../assets/img/user.png";
import StatusWithHooks from "./Status/StatusWithHooks";
import { usersAPI } from "../../../api/api";
import { connect } from "react-redux";
import { compose } from "redux";
import {
  follow,
  unfollow,
  onShowMoreUsers,
  toggleIsFollowInProgress,
  followTC,
  unfollowTC,
} from "../../../redux/usersReducer";

const Bio = (props) => {
  const onAvatarSelected = (e) => {
    if (e.target.files.length) {
      props.savePhoto(e.target.files[0]);
    }
  };
  return (
    <div className={classes.bio}>
      <div className={classes.avatar}>
        <img
          src={props.bio.photos.large ? props.bio.photos.large : avatar}
          alt="bio-img"
        />
        {props.isOwner ? (
          <div className={classes.selectPhoto}>
            <label>
              <div>üì∑</div>
              <input type={"file"} onChange={onAvatarSelected}></input>
            </label>
          </div>
        ) : null}
      </div>
      <div className={classes.bio_content}>
        <h2 className={classes.name}>{props.bio.fullName}</h2>
        <br />
        <StatusWithHooks
          isOwner={props.isOwner}
          status={props.status}
          updateStatus={props.updateStatus}
        />
        <p>
          –î–µ–Ω—å —Ä–æ–∂–¥–µ–Ω–∏—è: {props.bio.birthday ? props.bio.birthday : "–Ω–µ —É–∫–∞–∑–∞–Ω"}
        </p>
        <p>–ì–æ—Ä–æ–¥: {props.bio.city ? props.bio.city : "–Ω–µ —É–∫–∞–∑–∞–Ω"}</p>
      </div>
      {!props.isOwner ? (
        <div className={classes.subscribe}>
          <button
            disabled={props.followInProgress}
            onClick={() => {
              props.followTC(props.bio.userId);
            }}
            className={`${classes.unfollow} button`}
          >
            –í—ã –ø–æ–¥–ø–∏—Å–∞–Ω—ã
          </button>
          <button
            disabled={props.followInProgress}
            onClick={() => {
              props.unfollowTC(props.bio.userId);
            }}
            className={`button`}
          >
            –ü–æ–¥–ø–∏—Å–∞—Ç—å—Å—è
          </button>
        </div>
      ) : null}
    </div>
  );
};

class BioContainer extends React.Component {
  render() {
    return <Bio {...this.props} />;
  }
}

let mapStateToProps = (state) => {
  return {
    followAPI: usersAPI.follow,
    unfollowAPI: usersAPI.unfollow,
  };
};

export default compose(
  connect(mapStateToProps, {
    follow,
    unfollow,
    onShowMoreUsers,
    toggleIsFollowInProgress,
    followTC,
    unfollowTC,
  })
)(BioContainer);
